import React, { useState } from "react";
import "./assets/css/jsx.css";
import "./assets/css/multipleselectbox.css";
// import './assets/css/themes/demos.css'
import "./assets/css/cupertino/jquery-ui-1.8.4.custom.css";
import solver from "javascript-lp-solver";
const App = () => {
  const [variables, setvariables] = useState(2);
  const [funcionObjetivo, setfuncionObjetivo] = useState([0, 0]);
  const [constraints, setconstraints] = useState([0, 0]);
  const [signo, setsigno] = useState("<=");
  const [lastConst, setlastConst] = useState(0);
  const [type, settype] = useState("max");
  const [lines, setlines] = useState([]);
  const [solution, setsolution] = useState({});
  const [factible, setfactible] = useState(true);
  const [showSolution, setshowSolution] = useState(false);
  const [result, setresult] = useState(0);

  console.log(funcionObjetivo);
  console.log(constraints);
  const updateRange = (value) => {
    setvariables(value);
    let vars = [];
    let cons = [];
    for (let index = 0; index < value; index++) {
      vars.push(0);
      cons.push(0);
    }
    console.log(vars);
    setconstraints(cons);
    setfuncionObjetivo(vars);
  };

  const updatefo = (value, index) => {
    let tempArray = funcionObjetivo;
    tempArray[index] = Number(value);
    setfuncionObjetivo([...tempArray]);
  };

  const updatecons = (value, index) => {
    let tempArray = constraints;
    tempArray[index] = Number(value);
    setconstraints([...tempArray]);
  };

  const addConstraint = () => {
    let linestemp = lines;
    let line = "";
    constraints.forEach((e, index) => {
      line = line + `${e}` + " " + `x${index + 1}` + " ";
    });
    line = line + " " + `${signo}` + " " + `${lastConst}` + "";
    linestemp.push(line);
    setlines([...linestemp]);
  };

  const resolverProblema = () => {
    let line = type == "max" ? "max: " : "min: ";
    funcionObjetivo.forEach((e, index) => {
      line = line + `${e}` + " " + `x${index + 1}` + " ";
    });

    let modelFormat = lines;
    modelFormat.unshift(line);
    modelFormat = solver.ReformatLP(modelFormat);
    let solution = solver.Solve(modelFormat);
    console.log(solution);
    setresult(solution.result);
    setfactible(solution.feasible);
    delete solution.feasible;
    delete solution.bounded;
    delete solution.result;
    delete solution.isIntegral;
    setsolution(solution);

    setshowSolution(true);
  };

  console.log(lines);

  return (
    <>
      <div id="cabecera" class="ui-state-default ui-corner-all">
        Javascript 2 Fases
      </div>
      <div class="image-container">
        <img
          src="https://upload.wikimedia.org/wikipedia/commons/thumb/3/3a/UNMSM_coatofarms_seal.svg/1200px-UNMSM_coatofarms_seal.svg.png"
          width="50"
          height="60"
        />
      </div>
      <div id="simplex_tabs">
        <ul>
          <li>
            <a href="#tab_simplex">2 Fases</a>
          </li>
        </ul>
        <div id="tab_simplex">
          <div id="jsx_funcion_objetivo">
            <div class="titulo ui-state-default ui-corner-all">
              Función objetivo
            </div>
            <div class="contenido ui-corner-all">
              <div class="btn-group">
                <input
                  type="radio"
                  class="btn-check"
                  name="btnradio"
                  autocomplete="off"
                  checked={type == "max"}
                />
                <label
                  class="btn btn-outline-danger"
                  onClick={() => settype("max")}
                >
                  Maximizar
                </label>

                <input
                  type="radio"
                  class="btn-check"
                  name="btnradio"
                  autocomplete="off"
                  checked={type == "min"}
                />
                <label
                  class="btn btn-outline-danger"
                  onClick={() => settype("min")}
                >
                  Minimizar
                </label>
              </div>
              <br />

              <label for="customRange1" class="form-label">
                Variables {variables}
              </label>
              <input
                type="range"
                value={variables}
                onChange={(e) => updateRange(Number(e.target.value))}
                class="form-range"
                id="customRange1"
                min="1"
                max="10"
              />

              <table>
                {funcionObjetivo.map((e, index) => {
                  return (
                    <tr>
                      <div
                        class="btn-group"
                        role="group"
                        aria-label="Basic radio toggle button group"
                      >
                        <input
                          type="text"
                          onChange={(e) => updatefo(e.target.value, index)}
                        />{" "}
                        <span style={{ marginLeft: 10 }}>
                          X <sub>{index + 1}</sub>
                        </span>
                      </div>
                    </tr>
                  );
                })}
              </table>
            </div>
          </div>
          <div id="jsx_restricciones">
            <div class="titulo ui-state-default ui-corner-all">
              Restricciones
            </div>

            <div class="contenido ui-corner-all">
              <table>
                {constraints.map((e, index) => {
                  return (
                    <tr>
                      <div
                        class="btn-group"
                        role="group"
                        aria-label="Basic radio toggle button group"
                      >
                        <input
                          type="text"
                          onChange={(e) => updatecons(e.target.value, index)}
                        />{" "}
                        <span style={{ marginLeft: 10 }}>
                          X <sub>{index + 1}</sub>
                        </span>
                      </div>
                    </tr>
                  );
                })}
              </table>
              <table id="jsx_res">
                <tr>
                  <td>
                    <div id="jsx_res_nueva">
                      <div id="jsx_res_fo_cont"></div>
                      <div id="jsx_res_fo_signo">
                        <div class="btn-group">
                          <button
                            type="button"
                            className={
                              signo === "<="
                                ? "btn btn-danger"
                                : "btn btn-outline-danger"
                            }
                            onClick={() => setsigno("<=")}
                          >
                            ≤
                          </button>
                          <button
                            type="button"
                            className={
                              signo === "="
                                ? "btn btn-danger"
                                : "btn btn-outline-danger"
                            }
                            onClick={() => setsigno("=")}
                          >
                            =
                          </button>
                          <button
                            type="button"
                            className={
                              signo === ">="
                                ? "btn btn-danger"
                                : "btn btn-outline-danger"
                            }
                            onClick={() => setsigno(">=")}
                          >
                            ≥
                          </button>
                        </div>
                        <input
                          type="text"
                          onChange={(e) => setlastConst(Number(e.target.value))}
                        />
                      </div>
                      <div id="jsx_res_fo_nombre">
                        <button
                          className="btn btn-danger"
                          onClick={() => addConstraint()}
                        >
                          Añadir
                        </button>
                      </div>
                    </div>
                  </td>
                  <td>
                    <div id="jsx_res_cont">
                      <div class="ui-state-default ui-corner-all">
                        Restricciones actuales
                      </div>
                      <ul class="list-group">
                        {lines.map((e) => {
                          return <li class="list-group-item">{e}</li>;
                        })}
                      </ul>
                    </div>
                  </td>
                </tr>
              </table>
            </div>
          </div>
          <div id="jsx_problema">
            <div class="titulo ui-state-default ui-corner-all">Problema</div>
            <div class="contenido ui-corner-all">
              <table>
                <tr>
                  <td id="jsx_pro_botonestd" rowspan="2">
                    <div id="jsx_pro_botones">
                      <button
                        className="btn btn-danger"
                        onClick={() => window.location.reload()}
                      >
                        Nuevo problema
                      </button>
                      <button
                        onClick={() => resolverProblema()}
                        className="btn btn-danger"
                      >
                        Resolver problema
                      </button>
                    </div>
                  </td>
                </tr>
                <tr></tr>
              </table>
            </div>
          </div>
          <div id="jsx_solucion">
            <div class="titulo ui-state-default ui-corner-all">Solución</div>
            {showSolution && (
              <b>{factible ? "Es factible" : "No es Factible"}</b>
            )}
            {showSolution && factible && (
              <div>
                <h2>Resultados:</h2>
                <b>El valor optimo es</b>
                <p>Z={result}</p>
                <b>Con los valores de:</b>
                <div>{JSON.stringify(solution)}</div>
              </div>
            )}
          </div>
        </div>
      </div>
    </>
  );
};

export default App;
